import { Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { LotterService } from '../lotter.service';

@Component({
  selector: 'app-lottery',
  templateUrl: './lottery.component.html',
  styleUrls: ['./lottery.component.css'],
})
export class LotteryComponent implements OnInit {
  @Input() lotteryData: any;

  lotPersons = [];
  winnerArray = [];

  showNoWinner = false;

  isLoading = false;

  constructor(public _lotterService: LotterService) {}

  ngOnInit() {
    this._lotterService.sendPerson$.subscribe((res) => {
      this.lotPersons = [];
      if (res && res.length > 0) {
        this.changeLoading(true, 40);
        // console.log('sendPerson lottery', res);
        this.lotPersons = res;
        this.findWinner();
      } else {
        this.winnerArray = [];
        this.isLoading = false;
        this.showNoWinner = false;
        this.changeLoading(false, 0);
      }
    });
  }

  changeLoading(val, time){
    setTimeout(() => { this.isLoading = val }, time);
  }


  

  findWinner() {
    this.winnerArray = [];
    if (this.lotPersons.length === 0) return;
    this.lotPersons.forEach((p) => {
      // if (this.lotteryData.lotNumber.indexOf(p.lastNum) !== -1) {
      //   this.winnerArray.push(p.name);
      // }
      let data = this.lotteryData.lotNumber;
      for (let i = 0; i < data.length; i++) {
        let numLen = data[i].length;
        let lastNumber = p.lastNum.slice(-numLen);
        if (data[i].indexOf(lastNumber) !== -1) {
          this.winnerArray.push(p);
        }
      }
    });
    // console.log('winner',this.winnerArray);

    if(this.winnerArray.length === 0 ) {
      this.showNoWinner = true;
    } else {
      this.showNoWinner = false;
    }
    this.changeLoading(false, 400);
  }
}
